export default {
	data() {
		return {
			pagination: 1,
			work: [],
			loadingText: '上拉加载更多',
			scrollHeight: 0
		}
	},
	onLoad() {
		let _this = this
		uni.getSystemInfo({
			success(res) {
				_this.scrollHeight = res.windowHeight
			}
		})
		uni.request({
			url: "https://m.jianzhimao.com/job/pageDirect?page=" + this.pagination + "&city=%E5%B9%BF%E5%B7%9E",
			method: "GET",
			dataType: "json",
			success: (res) => {
				console.log(res)
				this.work = res.data.data
				console.log(this.work)
			}
		})
		
		
	},
	methods: {
		lower() {
			this.pagination = this.pagination + 1
			this.loadingText = '加载中'
			uni.request({
				url: "https://m.jianzhimao.com/job/pageDirect?page=" + this.pagination + "&city=%E5%B9%BF%E5%B7%9E",
				method: "GET",
				dataType: 'json',
				success: (res) => {
					if (res.data.data.length <= 0) {
						this.loadingText = '我是有底线的'
						return false
					}
					this.work = this.work.concat(res.data.data),
						this.loadingText = '上拉加载更多'

				}
			});
		}
	}
}
